@import '../../../common/styles/variables';
@import '../../../common/styles/mixins';


.titleWrapper  {
  position: relative;

  &::before {
    position: absolute;
    content: '';
    width: 55px;
    height: 1px;
    background-color: $primaryColor;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}
.title{
  @include title(1.3,$textColorPrimary,600,45px);
}
